
.postContainer{
    width: 80%;
    margin: 0 auto;
    height: 100vh;
    padding-top: 50px;
}

.postContainer h2{
    font-weight: 300;
    text-align: center;
    font-size: 35px;
}

.postContainer input{
    font-size: 17px;
    width: 90%;
    margin-top: 10px;
    padding: 12px 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-weight: 300;
}

.postContainer button{
    background: #03a9f4;
    font-size: 13px;
    padding: 10px;
    color: #fff;
    margin-top: 10px;
}

.postContainer select {
    font-size: 20px;
    margin-top: 10px;
}


